<template>
  <div class="actor">
    <div class="basic">
      <p class="nickName">昵称: {{ data.nickName }}</p>
      <p class="episode">第{{ data.episode }}期，第{{ data.num }}人</p>
      <button v-on:click="pass">通过下墙</button>
      <button v-on:click="refuse">驳回下墙</button>
    </div>
    <div class="expand">
      <img
        :class="ifShow == true ? 'img-anim' : 'expand-img'"
        v-on:click="rotate"
        class="expand-img"
        src="../assets/展开.svg"
      />
    </div>
    <div v-show="ifShow">
      <p>照片</p>
      <div class="pic-content">
        <img v-for="(item, index) in data.imgUrl" :key="index" src="item" />
      </div>
      <div class="sub-info">
        <p>性别</p>
        <p>学校</p>
        <p>年龄</p>
        <p>身高</p>
        <p>星座</p>
        <p>家乡</p>
        <p>{{ data.sex }}</p>
        <p>{{ data.school }}</p>
        <p>{{ data.age }}</p>
        <p>{{ data.height }}</p>
        <p>{{ data.constellation }}</p>
        <p>{{ data.place }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Actor",
  props: ["data"],
  methods: {
    rotate: function() {
      this.ifShow = !this.ifShow;
    },
    pass: function() {
      this.axios.post("url", {
        ID: this.data.openid,
      });
    },
    refuse: function() {
      this.axios.post("url", {
        ID: this.data.openid,
      });
    },
  },
  data: function() {
    return {
      ifShow: false,
    };
  },
};
</script>

<style scoped>
.actor {
  margin-bottom: 20px;
  padding: 10px;
}
.basic {
  display: grid;
  grid-template-columns: 200px 100px;
  grid-template-rows: 40px 40px;
  grid-auto-flow: column;
  align-content: center;
  justify-content: center;
  grid-gap: 10px;
}
.nickName {
  margin: 0;
  width: 200px;
  text-align: left;
  line-height: 40px;
}
.episode {
  margin: 0;
  width: 200px;
  text-align: left;
  line-height: 40px;
}
button {
  height: 40px;
}
.expand-img {
  width: 30px;
  height: 30px;
}
.img-anim {
  transform: rotate(180deg);
  width: 30px;
  height: 30px;
}
.pic-content {
  max-width: 300px;
  border-style: solid;
  border-width: 2px;
  border-radius: 10px;
  width: 95%;
  height: 200px;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  margin: 0 auto;
}
.sub-info {
  display: grid;
  grid-template-columns: 100px 100px;
  grid-template-rows: repeat(6, 30px);
  grid-auto-flow: column;
  justify-content: center;
  align-content: center;
  gap: 10px;
}
</style>
